.mainContainder {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 40px 0 60px;
}

.mainContainderHeader {
  font-size: 45px;
  margin: 20px 0 30px;
  font-weight: 500;
  text-shadow: 3px 4px 6px rgba(66, 68, 90, 1);
}

.mainServicesList {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 35px;
  padding: 0;
  margin: 0;
}

.mainServicesList li {
  width: 250px;
  padding: 20px 20px;
  text-align: center;
  background-color: rgba(100, 100, 100, 0.233);
  border-radius: 8px;
  box-shadow: 8px 10px 30px 7px rgba(66, 68, 90, 1);
}
